	module.exports = {"data":{"site":{"siteMetadata":{"title":"Stephen Quick","author":"Stephen Quick"}},"markdownRemark":{"id":"c:/Users/stephen/Documents/Git/stepquick.github.io/src/pages/upgraded-to-octopress-3/index.md absPath of file >>> MarkdownRemark","html":"<p>I finally got around to upgrading to <a href=\"http://www.github.com/octopress/octopress.git\">Octopress 3</a> and honestly it’s a noticeable difference.</p>\n<p>For starters, it’s very clean, and doesn’t include nearly as much boilerplate as the older version does. It also seems to include default jekyll commands, as well as a default jekyll theme. Deploy also works amazing, it’s exactly what I was wanting. I hated having issues with deploying from multiple computers.</p>\n<p>I also need to do some house cleaning for this site. A few things that stick out are:</p>\n<ul>\n<li>Convert to using a more handmade theme/something nicer. Jekyll default theme is fairly nice, but I have to add more stuff that I enjoyed from the older octopress theme.</li>\n<li>Add more content, I’ve got a bunch of things I’d like to remember from working recently, and this is as good a place as any to store these things.</li>\n</ul>","frontmatter":{"title":"Upgraded to Octopress 3","date":"September 16, 2015"}}},"pathContext":{"slug":"/upgraded-to-octopress-3/","previous":{"fields":{"slug":"/installing-homestead-on-ubuntu/"},"frontmatter":{"title":"Installing Homestead on Ubuntu"}},"next":{"fields":{"slug":"/how-to-disable-chrome-web-security-for-development/"},"frontmatter":{"title":"How to disable Chrome Web Security for development"}}}}

